.container {
  text-align: center;
}
.toggle-switch {
  position: relative;
  width: 63%;
  display: inline-block;
  text-align: left;
  margin-top: 8px;
  margin-bottom: 8px;
}
.checkboxts {
  display: none;
}
.labelts {
  display: block;
  overflow: hidden;
  cursor: pointer;
  border: 0 solid #bbb;
  border-radius: 25px;
}
.inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.3s ease-in 0s;
}
.inner:before,
.inner:after {
  float: left;
  width: 50%;
  height: 36px;
  padding: 0;
  line-height: 36px;
  color: #fff;
  font-weight: bold;
  box-sizing: border-box;
}
.inner:before {

    content: "Redeem";
  padding-right: 30%;
  background-color: #FF0000;
  color: #fff;
  text-align: right;



}
.inner:after {
  content: "Invest";
  padding-right: 30%;
  background-color: #060;
  color: #fff;
}
.switch {
  display: block;
  width: 24px;
  margin: 5px;
  background: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  right: 80%;
  border: 0 solid #bbb;
  border-radius: 25px;
  transition: all 0.3s ease-in 0s;
}
.checkboxts:checked + .labelts .inner {
  margin-left: 0;
}
.checkboxts:checked + .labelts .switch {
  right: 0px;
}